import React, { useState, useEffect } from "react";
import "./header.css";
import { nav } from "../../data/Data";
import { Link, useLocation } from "react-router-dom";
import { BtnChoose } from "./btn-choose";
import { BtnLogin } from "./btn-login";

const Header = ({ language, setLanguage }) => {
  const [navList, setNavList] = useState(false);
  const [loading, setLoading] = useState(false); //  
  const homepage = process.env.PUBLIC_URL || "/"; //   
  const location = useLocation(); //  useLocation  react-router-dom

  useEffect(() => {
    //       
    handleLoading();
  }, []);

  useEffect(() => {
    //       ()
    handleLoading();
  }, [location.pathname]); //  location.pathname   

  const scrollToTop = () => {
    window.scrollTo(0, 0); //    
  };

  //    
  const handleLoading = () => {
    setLoading(true);
    //   setTimeout      
    setTimeout(() => {
      setLoading(false);
    }, 1000); //  1000  1 
  };

  return (
    <header>
      <div className="container flex">
        <div className="logo">
          <Link to={homepage}>
            <img src={`${homepage}/images/logo-2030.png`} alt="logo" />
          </Link>
        </div>

        <div className="nav">
          <ul className={navList ? "small" : "flex"}>
            {nav.map((item, index) => (
              <li
                onClick={() => (navList ? setNavList(!navList) : null)}
                key={index}
              >
                <Link
                  to={item.path}
                  onClick={() => {
                    scrollToTop();
                  }}
                >
                  {language === "arabic" ? item.text_ar : item.text_en}
                </Link>
              </li>
            ))}

            {navList && (
              <li>
                <li>
                  <BtnLogin language={language} />
                  <BtnChoose language={language} setLanguage={setLanguage} />
                </li>
              </li>
            )}
          </ul>
        </div>

        <div className="button flex">
          <BtnLogin language={language} />
          <BtnChoose language={language} setLanguage={setLanguage} />
        </div>

        <div className="toggle">
          <button onClick={() => setNavList(!navList)}>
            {navList ? (
              <i className="fa fa-times"></i>
            ) : (
              <i className="fa fa-bars"></i>
            )}
          </button>
        </div>
      </div>

      {/*        true */}
      {loading && (
        <div className="loader">
          <img src={`${homepage}/favicon.png`} alt="" srcset="" />
          <img
            className="circle"
            src={`${homepage}/lode.png`}
            alt=""
            srcset=""
          />
        </div>
      )}
    </header>
  );
};

export default Header;
