import React from "react";
import { Link } from "react-router-dom"; //   Link   react-router-dom

export function BtnLogin({ language }) {
  return (
    <Link to="/login" className="btn1 btn-login">
      {" "}
      {/*         */}
      <i className="fa fa-sign-out"></i>{" "}
      {language === "arabic" ? " " : "Login"}
    </Link>
  );
}
